.button__bg {
  position: absolute;
  top: -1px;
  right: -1px;
  bottom: -1px;
  left: -1px;
  overflow: hidden;
  border-radius: inherit;
  isolation: isolate;
}

.button__bg:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 110%;
  height: 330%;
  background: #ebffff;
  border: inherit;
  border-radius: 50%;
  transform: translate(-50%, -75%) scale(0.14);
  opacity: 0;
  transition: opacity 0.3s ease-out, transform 0.3s ease-out 0.3s, border 0.15s ease;
  aspect-ratio: 1;
}

.button__bg:after {
  content: attr(data-text);
  position: absolute;
  top: 50%;
  left: 50%;
  color: inherit;
  transform: translate(-50%, -200%);
  opacity: 0;
  transition: opacity 0.3s ease-out, transform 0.3s ease-out;
}

