  justify-content: center;
  width: 40px;
  height: 40px;
  background: #5317a6;
  border-radius: 50%;
  transform: translate(-50%, -50%) scale(0.5);
  transform-origin: 50% 50%;
  opacity: 0;
  transition: opacity 0.2s ease, transform 0.12s ease;
}

.hamburger.is-active .hamburger__bg {
  transform: translate(-50%, -50%);
