    transition: opacity 0.3s ease-out, transform 0.3s ease-out, border 0.15s ease;
  }

  .button:hover .button__bg:after {
    transform: translate(-50%, -50%);
    opacity: 1;
  }

  .button:hover .button__text {
    transform: translateY(100%);
    opacity: 0;
  }

  .button--fill:hover {
    color: #000;
    background: #ebffff;
  }

  .button--outline:hover {
    color: #fff;
    background: #02b7b9;
    border: 1px solid #02b7b9;
  }

  .button--outline:hover svg {
    fill: #02b7b9;
  }

  .button--secondary:hover {
    color: #fff;
    background: #9747ff;
  }

  .button--secondary.button--outline:hover {
    color: #fff;
    background: #6800f0;
    border: 1px solid #6800f0;
  }

  .button--secondary.button--outline:hover svg {
    fill: #fff;
  }

  .button--white.button--outline:hover {
    color: #000;
    background: #fff;
    border: 1px solid #fff;
  }

  .button--white.button--outline:hover .button__bg:before {
    background-color: #fff;
  }

  .button--white.button--outline:hover svg {
    fill: #000;
  }

  .button--flat:hover {
    background-color: transparent;
    color: #6194f6;
  }

  .button--flat:hover svg {
    fill: currentColor;
  }

  .button--flat.button--primary:hover {
    color: #02b7b9;
  }

  .lang-switcher:hover {
    color: #fff;
    background: #5317a6;
  }

  .lang-switcher:hover .lang-switcher__list {
    transform: translateY(0);
    opacity: 1;
    pointer-events: auto;
  }

  .gradient-button:hover {
    color: #fff;
  }

  .gradient-button:hover:before {
    transform: scale(1);
  }

  .wave-card:hover .wave-card__waves {
    transform: scale(1.1);
  }

  .price-card:hover {
    background: #3d0d7e;
  }

  .price-card:hover .price-card__waves {
    transform: translate(-20px, -20px);
  }

  .accordion-item:hover {
    background-color: #fff;
    opacity: 1 !important;
  }

  .post-card:hover {
    background-color: #fff;
    box-shadow: 0 10px 25px 0 rgba(37, 0, 86, 0.1);
  }

  .video:hover .video__preview:after {
    opacity: 0.4;
  }

  .video:hover .video__play-btn {
    transform: translate(-50%, -50%) scale(1.05);
  }

  .wave-image-card:hover .wave-image-card__waves {
    transform: scale(1.1);
  }

  .wave-image-card__footer a:hover {
    color: #00aaab;
  }

  .wave-quote:hover .wave-quote__waves {
    transform: scale(1.1);
  }

  .product-card:hover .product-card__title {
    color: #5317a6;
  }

  .product-card--large:hover .product-card__title {
    color: #000;
  }

  .entry-card:hover .entry-card__title {
    color: #02b7b9;
  }

  .entry-card:hover .entry-card__image img {
    transform: scale(1.1);
  }

  .swiper-thumbnail__slide:hover {
    border-color: rgba(0, 0, 0, 0.4);
  }

  .back-to-top:hover {
    background-color: #02b7b9;
  }

  .star-input svg:hover {
    fill: #ffc700;
  }

  .tel__number:hover {
    color: #6800f0;
  }

  .nav .is-primary:hover {
    color: #02b7b9;
  }

  .nav__item:hover {
    color: #5317a6;
  }

  .nav__child .nav__item:hover {
    color: #5317a6;
  }

  .footer__email:hover {
    color: #4900f3;
  }

  .footer-nav__item:hover {
    color: #9747ff;
  }

  .entry-content a:hover {
    color: #6194f6;
  }

  .section-faq__form:hover .section-faq__form-wave {
    transform: translate(0, -30px) scale(0.9);
  }

  .pagination__item:hover {
    color: #fff;
    background-color: #02b7b9;
  }

  .pagination__item:hover svg {
    fill: #fff;
  }

  .entry-sidebar-card:hover .entry-sidebar-card__title {
    color: #02b7b9;
  }

  .docs-sidebar-hamburger:hover {
    background-color: #02b7b9;
  }
}

@media (pointer: fine) {
  .social:active {
    background: #481490;
  }

  .button--fill:active {
    color: #000;
    background: #ebffff;
  }

  .button--outline:active {
    color: #00aaab;
    background: rgba(0, 119, 120, 0.025);
    border: 1px solid #007778;
  }

  .button--outline:active svg {
    fill: #02b7b9;
  }

  .button--secondary:active {
    color: #fff;
    background: #481490;
  }

  .button--secondary.button--outline:active {
    color: #fff;
    background: #6800f0;
    border: 1px solid #6800f0;
  }
}

@media (hover: hover) {
  .select-control {
    display: flex;
  }
}